  methods: {
     timestampToTime(timestamp) {
        var time=''
        if(timestamp.toString().length>11){
          time=timestamp
        }else{
          time=timestamp*1000
        }
        var date = new Date(time); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var D = date.getDate() > 10 ? date.getDate() + ' ' : ('0' + date.getDate()) + ' ';
        var h = date.getHours() > 10 ? date.getHours() + ':' : ('0' + date.getHours()) + ':';
        var m = date.getMinutes() > 10 ? date.getMinutes() + ':' : ('0' + date.getMinutes()) + ':';
        var s = date.getSeconds() > 10 ? date.getSeconds() : ('0' + date.getSeconds());
        return Y + M + D + h + m + s;
    },
     complete() {
       var that = this
      this.$socket.on("match_trade", function(msg) {
        if (msg.type == "match_trade") {
          // console.log(msg,111111111);
          // console.log(msg.data[0].ts)
          var times = that.timestampToTime(msg.data[0].ts)
           msg.data[0].ts= that.timestampToTime(msg.data[0].ts)
          if(msg.data[0].direction == "sell"){
            that.sell.unshift(msg);
            if(that.sell.length>10){
              that.sell.splice(0,that.sell.length-5);
            }
          }
          if(msg.data[0].direction == "buy"){
            that.buy.unshift(msg);
            if(that.buy.length>10){
