          //   var times = that.timestampToTime(msg.data[0].ts)
          //   var way;
          //   msg.data[0].direction == 'buy' ? way=1 : way=2;
          //   var data ={
          //     price: msg.data[0].price,
          //     time: times,
          //     number: msg.data[0].amount,
