    connect() {
      var that = this;
      that.$socket.emit("login", localStorage.getItem("user_id"));
      that.$socket.on("daymarket", msg => {
        if (msg.type == 'daymarket') {
         
          var cname = msg.symbol.replace("/","_");;
          var change = msg.change;
          var now_price = msg.now_price;
          var volume = msg.volume;
         $('.'+cname).find(".now_price").html((now_price -0).toFixed(4));//最后成交价
         $('.'+cname).find(".volume1").html((volume-0).toFixed(4));//24小时成交量
         $('.'+cname).find(".baifen").html(change+'%');//24小时成交量
         $('.'+cname).find(".max").html((msg.high-0).toFixed(4)|| 0 );//24小时成交量
          $('.'+cname).find(".min").html((msg.low-0).toFixed(4)|| 0);//24小时成交量
          $("div[data-name='" + cname + "']")
            .find(".now_cny")
            .html((now_price *6.9641).toFixed(4)+' CNY');
          if(change<0){
             $('.'+cname).find(".baifen").addClass('redC');
          }else{
            $('.'+cname).find(".baifen").addClass('buleC');
          }
           
        }
      });
    },
    setPercent(a, b) {
      if (a - b == 0) {
        return "0%";
      } else if (a == 0) {
        return "-100%";
      } else if (b == 0) {
        return "+100%";
      } else {
        var p = ((a - b) / b / 100).toFixed(2);
        if (p > 0) {
          p = "+" + p + "%";
        } else {
