  // top: 0;
  // height: 100%;
  // width: 10px;
  // display: flex;
  // flex-direction: column;
  // justify-content: center;
}
div.swiper-container .swiper-pagination > span {
  // display: block;
  background: #fff;
  border-radius: 0;
  width: 56px;
  height: 3px;
  // opacity: 0.5;
  // margin: 10px 0;
}
div.swiper-container .swiper-pagination-bullet-active {
  background: transparent;
  border: 2px solid #fff;
}
.up-clr {
  color: #12b886;
}
.down-clr {
  color: #cc4951;
}
/* 币种列表 */
.coins-list {
  margin: 10px auto;
  width: 1500px;
  line-height: 40px;
  text-align: center;
  // border: 1px solid #4e5b85;
  .coin-tab {
    height: 42px;
    color: #c7cce6;
    display: flex;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    > ul {
      display: flex;
      li {
        padding: 0 40px;
        font-size: 14px;
        // box-shadow: 0 0 1px hsla(231, 9%, 54%, 0.2);
        // border-bottom: 1px solid #4e5b85;
        // border-right: 1px solid #4e5b85;
      }
      .activeCoin {
        border-bottom: none;
      }
    }
  }
  .list-title {
    display: flex;
    > span {
      flex: 1;

      text-align: center;
      color: #8b89c8;
      font-size: 14px;
    }
  }
  .list-con {
    // background: rgb(32, 36, 55);
    max-height: 680px;
    overflow: auto;
    &::-webkit-scrollbar {
      display: none;
    }
    li:nth-child(2n) {
      background: rgb(24, 24, 76);
    }
    li:nth-child(2n + 1) {
      background: rgb(34, 31, 97);
    }
    li {
      display: flex;
      // border-bottom: 1px solid #282e44;
      color: #c7cce6;
      &:hover {
        background: rgb(22, 22, 68);
      }
      > div {
        flex: 1;
      }
    }
  }
}
.carousel .swiper-slide:hover {
  background-color: #327add;
}
.nav_left,
.con_left {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.withdraw {
  margin: 0 4px;
}
.carousel .last a:hover {
  cursor: pointer;
}
.sliders {
  width: 100%;
  max-height: 540px;
  overflow: hidden;
  img{width: 100%;display: block;}
  @media screen and (max-width:1560px){
    // height: 465px;
    // img{
    //   width: 100%;
    //   height: 465px;
    // }
  }
}
@media screen and (max-width:1410px){
  // .mt{margin-top:5px;}
}
.swiper-container-horizontal>.swiper-pagination-bullets{
  bottom: 25px;
}
.swiper-pagination-bullet{
  width: 56px!important;
  height: 3px;
  border-radius: 0;
  background: #fff;
}

.notice_ul {
  margin: 0 40px;
  padding: 5px 0;
  // background: #171c2b;
  overflow: hidden;
  word-break: keep-all;
}
.notice_ul > li {
  // flex: 1;
  font-size: 12px;
  padding: 0 15px;
  text-align: center;
  color: #9eb5ca;
  word-break: keep-all;
  position: relative;
}
.notice_ul > li::after {
  content: "/";
  position: absolute;
  left: -0.5em;
  color: #9eb5ca;
}
.notice_ul > li:last-child:after {
  content: "";
  color: #6b80ae;
}
.notice_ul > li:hover {
  color: #6b80ae;
  cursor: pointer;
}
.feature_wrap {
  background-color: #fff;
  padding: 90px 0;
  text-align: center;
  color: #54748f;
  line-height: 2em;
}
.feature_wrap h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #192544;
}
.feature_wrap .feature_list {
  padding-top: 80px;
  width: 70%;
  margin: 0 auto;
}
.feature_wrap .feature_list li {
  float: left;
  width: 33%;
  background: transparent none no-repeat top;
  // padding-top: 140px;
  padding-top: 70px;
}
.feature_wrap .feature_list li.feature_safe {
  // background-image: url(../../static/imgs/feature_safe.8e76904.svg);
  background-image: url(../../static/imgs/ppp0.svg)
}
.feature_wrap .feature_list li.feature_eco {
  // background-image: url(../../static/imgs/feature_eco.4f174e6.svg);
  background-image: url(../../static/imgs/ppp1.svg)

}
.feature_wrap .feature_list li.feature_user {
  // background-image: url(../../static/imgs/feature_user.7002f27.svg);
  background-image: url(../../static/imgs/ppp2.svg)

}
.feature_wrap h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #192544;
}
.notice_box{
    width: 100%;
    height: 52px;
    border-top: 1px solid rgba(29,50,66,.7);
    margin-top: -1px;
    background-color: #f6f9fc;
    position: relative;
    >div{
      margin: 0 30px;
      font-size: 12px;
      line-height: 52px;
      height: 36px;
      overflow: hidden;
      word-break: keep-all;
      text-align: center;
      span{
        display: inline-block;
        position: relative;
        padding: 0 2em 0 1.5em;
        color: #54748f;
        cursor: pointer;
      }
      span:before {
        content: "/";
        position: absolute;
        left: -.5em;
        color: #54748f;
      }
      span:nth-child(1):before{
        content: "";
      }
      span:hover{
        color: #357ce1;
      }
    }
}
.client_wrap{
  width: 100%;
  height: 710px;
  color: #9eb5ca;
  text-align: center;
  line-height: 2em;
  background-color: #fff;
  .wrap_in{
    height:700px;
    padding-top: 100px;
    // background-color: #152940;
    background: url(../../static/imgs/blue.svg) no-repeat;
    background-size: cover;

    h2{
      margin-bottom: 24px;
      color: #fff;
      font-weight: 400;
      font-size: 28px;
    }
    .nextp{color:#9eb5ca}
    ul{
      width: 810px;
      margin: 0 auto;
      text-align: left;
      justify-content: space-around;
      li{
        margin: 0 15px;
        color: #9eb5ca;
        padding: 40px 0;
        line-height: 48px;
        position: relative;
        cursor: pointer;
        p{
          height: 48px;
          width: 48px;
          margin: 0 10px 0 0;
          vertical-align: top;
          border-radius: 50%;
          background: rgba(53,124,225,.1) none no-repeat 50%;
          background-image: url(../assets/images/p0.svg)
        }
        .p1{
          background-image: url(../assets/images/p1.svg)
        }
        .p2{
          background-image: url(../assets/images/p2.svg)
        }

        .codebox{
          position: absolute;
          left: 15%;
          top: 100px;
          -webkit-transform: translate(-50%);
          -ms-transform: translate(-50%);
          transform: translate(-50%);
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          width: 120px;
          height: 130px;
          border: 1px solid #357ce1;
          border-radius: 2px;
          background: #fff none no-repeat 12px 12px/96px 96px;
          text-align: center;
          color: #357ce1;
          font-size: 12px;
          line-height: 18px;
          z-index: 6;
          display: none;
          img{
            width: 100px;
            height: 100px;
            margin: 0 auto;
            display:block;
            margin-top: 10px;
          }
        }
      }
      li:hover .codebox{
        display: block;
      }
    }
    .mon_mind{
      width: 518px;
      position: relative;
      margin: 0 auto;
      >div{
        width: 100%;
        .pcimg{
          width: 518px;
          margin: 0 auto;
        }
      }
      .mobimg{
        width: 122px;
        position: absolute;
        right: -60px;
        bottom: -20px;
        width: 122px;
        height: 253px;
        background: #0a151e none no-repeat 50%/cover;
        -webkit-box-shadow: -9px 12px 62px 0 rgba(36,52,69,.3);
        box-shadow: -9px 12px 62px 0 rgba(36,52,69,.3);
        border-radius: 16.47px;
      }
    }
  }
}
.register_wrap{
  height: 240px;
  width: 100%;
  // border-top: 1px solid #cad7e0;
  background: #fff;
  h2{
    color: #232a4a;
    font-size: 22px;
    line-height: 24px;
  }
  .rebox{
    margin: 32px auto 0 auto;
  }
  .input-register{
    width: 400px;
    height: 40px;
    font-size: 12px;
    color: #000;
    border: 1px solid #bdced9;
    border-radius: 2px;
    padding-left: 16px;
  }
  button{
    margin-left: 8px;
    display: inline-block;
    height: 40px;
    padding: 0 44px;
    font-size: 16px;
    min-width: 120px;
    -webkit-appearance: none;
    background-color: #357ce1;
    color: #fff;
    border: none;
    outline: none;
    border-radius: 2px;
    cursor: pointer;
  }
  button:hover{
    opacity: .8;
  }
}
}
</style>





